<template>
	<div>
		<HeaderComponent :view-title="settingTitle"></HeaderComponent>
		<div id="main-container" class="clear">
			<div class="main-list portrait">
				<p class="left">头像</p>
				<p class="right"><img :src="userImg"></p>
			</div>
			<div class="main-list nickname">
				<p class="left">昵称</p>
				<p class="right">大海的鱼儿</p>
			</div>
			<div class="main-list registration">
				<p class="left">注册时间</p>
				<p class="right">2018-08-22 09:58:51</p>
			</div>
			<div class="main-list phone">
				<p class="left">绑定手机号</p>
				<p class="right">
					<router-link to="/phone">
						<span>未绑定</span>
						<svg class="iconSetting" aria-hidden="true">
	  						<use xlink:href="#icon-jiantouyou"></use>
						</svg>
					</router-link>
				</p>
			</div>
			<div class="main-list vip">
				<p class="left">会员类型</p>
				<p class="right">普通会员</p>
			</div>
			<div class="main-list power">
				<p class="left">会员期限</p>
				<p class="right">无限制</p>
			</div>
		</div>
		<!-- <router-view></router-view> -->
	</div>
</template>

<script type="text/javascript">
	import HeaderComponent from '../../components/header/header';
	export default {
		settingTitle:Object,
		userImg:String,
		data(){
			return{
				settingTitle:{
					name:'账户设置',
					showCode:1
				},
				userImg:require('../../assets/images/user.jpg')
			}
		},
		mounted(){},
		components:{
			HeaderComponent
		}
	}
</script>
<style lang="scss" scoped="">
	@import '../../style/mixin';
	#main-container{
		width:100%;
		margin-top:2.2rem;
		div.main-list{
			width:100%;
			height:2.5rem;
			line-height: 2.5rem;
			border-bottom: 1px solid #D9D9D9;
			font-size:0.7rem;
			padding:0 0.4rem;
			background:#fff;
			p{
				width:50%;
				&.left{
					color:#6F6F6F;
				}
				&.right{
					text-align: right;
					color:#A1A1A1;
				}
			}
		}
		div.portrait{
			height:3rem;
			line-height: 3rem;
			p.right img{
				width:2.5rem;
				height:2.5rem;
				display:block;
				position:absolute;
				right:0.4rem;
				border:1px solid #A1A1A1;
				@include borderRadius(50%);
			}
		}
		div.registration,div.phone{
			margin-bottom:0.5rem;
		}
		div.phone p.right span{
			color:#A1A1A1;
		}
	}
	.iconSetting {
	   width: 0.9rem; height: 0.9rem;
	   vertical-align: -0.15em;
	   fill: currentColor;
	   overflow: hidden;
	   color:#A1A1A1;
	   position:relative;
	   top:0.1rem;
	}
</style>


